import { VueRouter } from "@lcp/xcommon";

import EmptyPage from "./EmptyPage.vue";
const { createRouter, createWebHashHistory } = VueRouter;

//emptyRouter
//默认空路由
export const emptyRouter = createRouter({
    history: createWebHashHistory(),
    routes: [
        {
            name: "index",
            path: "/:pathMatch(.*)*",
            component: EmptyPage,
        },
    ],
});