import { watch, nextTick, type WritableComputedRef } from 'vue';
import { AbilityRegister, Hooks, Utils } from '@lcp/xrenderer';
import { VueI18n } from '@lcp/xcommon'

const { createI18n: _createI18n } = VueI18n;
const { WidgetI18n } = Hooks;
const { CommonHttp } = Utils;

//默认的语言
const DEFAULT_LANG = "zh_CN";

//提供多国语言的翻译脚本
const createI18n = (messages: any) => {
  const i18n = _createI18n({
    legacy: false,
    allowComposition: true,
    fallbackLocale: DEFAULT_LANG,
    locale: DEFAULT_LANG,
    messages: messages, //从window当中获取语言
  });

  //注册翻译方法到表达式
  //翻译方法
  const translate = (key: string) => {
    return ((i18n.global as any).t as ((k: string) => string))(key);
  };


  //让表达式支持翻译方法
  AbilityRegister.registFunction("T", translate);
  AbilityRegister.registFunction("t", translate);


  return i18n;
}


//加载远程的 i18n接口
const DEFAULT_I18N_API = "/api/Auth/GetI18n";


const setI18nLanguage = (i18n: VueI18n.I18n, locale: string) => {
  if (i18n.mode === "legacy") {
    i18n.global.locale = locale;
  } else {
    (i18n.global.locale as WritableComputedRef<string>).value = locale;
  }
  window.document.querySelector("html")?.setAttribute("lang", locale);
}

const loadLocaleMessages = async (i18n: VueI18n.I18n, locale: string, i18nApi: string) => {
  const message = await CommonHttp.request({
    url: i18nApi,
    reqData: {
      lang: locale,
    },
  });

  i18n.global.setLocaleMessage(locale, message);

  return nextTick();
}


const setupI18n = () => {

  const i18n = createI18n({});

  //同步切换i18n插件的语言
  const lang = WidgetI18n.useCurrentLang();
  watch(
    lang,
    async (newVal, _oldVal) => {
      if (newVal) {
        await loadLocaleMessages(i18n, newVal, DEFAULT_I18N_API);
        setI18nLanguage(i18n, newVal);
      }
    },
    {
      immediate: true,
    }
  );

  return i18n;
}


export { createI18n, setupI18n }