import { type App } from "vue";

import { XRenderer, Utils, AbilityRegister } from "@lcp/xrenderer";

import "@lcp/xcomponents/dist/style.css";
import { XComponentsPlugin } from "@lcp/xcomponents";

import "@lcp/xwidgets/dist/style.css"
import { WidgetsPlugin as BaseWidgetsPlugin } from "@lcp/xwidgets";

import './monaco';
import '@lcp/xdesigner/dist/style.css'

import { createI18n } from './mock/i18n';
import zh_CN from './mock/i18n/locates/zh_CN.json';
import en_US from './mock/i18n/locates/en_US.json'

import { emptyRouter } from "./mock/router";

import { token as mockToken } from "./mock/token";


//模拟翻译接口保证不报错
const mockI18n = createI18n({
  zh_CN: zh_CN,
  en_US: en_US
});
//模拟路由
const mockRouter = emptyRouter

export default {
  install(app: App<unknown>) {

    //安装事例翻译
    app.use(mockI18n);

    //安装事例路由
    app.use(mockRouter);
    console.log(mockRouter, 'mockRouter')

    //安装通用组件库
    app.use(XComponentsPlugin);

    //安装渲染器
    app.use(XRenderer);

    //安装基本小部件库
    app.use(BaseWidgetsPlugin);

    //安装lcp路由
    // const lcpRouterPlugin = AbilityRegister.
    //   getWidget('c-router-app')?.
    //   Addition?.
    //   lcpRouterPlugin;
    // if (lcpRouterPlugin !== undefined && lcpRouterPlugin !== null) {
    //   app.use(lcpRouterPlugin);
    // }
    console.log(AbilityRegister);

    //设置mockToken
    Utils.Auth.login(mockToken);
    //或清除token
    //Utils.Auth.logout();

    //安装其他

  },
};
